.covid {
    max-width: 80%;
    margin: auto;
    display: flex;
    flex-direction: row;
    align-content: center;
    background-color: #ffffff;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 2px 5px;
  }
 .covid h4{
   font-size: 20px;
   font-weight: bold;
   text-align: left;
   padding: 10px 0px;
 } 
 .covid p{
   font-size: 12px;
   font-weight: bold;
   text-align: left;
 }
 .covid h5{
   font-size: 10px;
   text-align: left;
   font-weight: bold;
   margin: 20px 8px;
 }
 .covid #alag,i{
   color: #1f4691;
 }
  
  .covid img
  {
    max-width: 10.7vw;
    height: auto;
  }
  .covid .card img {
    max-width: 5.7vw;
    height: auto;
  }
  
  .covid .card {
    display: flex;
    flex-direction: row;
    margin: 0px 10px;
    padding: 0px 5px;
    border: none;

  }
  .covid .card .card1,
  .card2 {
    display: flex;
    flex-direction: row;
    background-color: #f2f7f7;
    margin: 2.47vh 5px;
    border-radius: 10px;
    border-color: #f2f7f7;
    padding: 2px 5px;
    
  }


  /* ------------------------------------------------------------------------------------------------ */
  
  .list{
