.promise{
  display: flex;
  flex-direction: column;
  max-width: 80%;
  margin: auto;
  text-align: center;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;

}
.promise h4{
  font-size:30px;
    font-family: 'Montserrat', sans-serif;
  font-weight: bolder;
  text-align: left;
  margin: 10px 0px;

}
.promise .parts{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  flex-wrap: wrap;
  /* flex-flow: row; */
  margin: 20px 10px;
  
}

.promise .parts .same_card{
  background-color: #f2f7f7;
  border: none;
  padding: 0px;
  min-width: 21vw;

}
/* .promise .parts .same_card */

.promise .parts .same_card .cathead h4{
  text-align: left;
  font-family: 'Montserrat', sans-serif;
font-family: 'Roboto', sans-serif;
  color: #707070;
  font-size: 20px;
  font-weight: bold;
}
.promise .parts .same_card p{
  text-align: left;
  font-family: 'Montserrat', sans-serif;
/* font-family: 'Roboto', sans-serif; */
  color: #707070;
  font-size: 15px;
  /* font-weight: bold; */
}
.promise .parts .same_card .logos{
 max-width: 13vw;

}

